import React, { forwardRef } from 'react';

type LabelTextFnProps = {} & React.LabelHTMLAttributes<HTMLLabelElement>;

type LabelTextFnRef = React.ForwardedRef<HTMLLabelElement>;

function LabelTextFn(props: LabelTextFnProps, ref: LabelTextFnRef) {
  return (
    <label
      {...props}
      ref={ref}
      className="mb-1 block text-sm font-medium text-gray-700"/>
  );
}

type LabelHeaderFnProps = {} & React.HTMLAttributes<HTMLHeadingElement>;

type LabelHeaderFnRef = React.ForwardedRef<HTMLHeadingElement>;

function LabelHeaderFn(props: LabelHeaderFnProps, ref: LabelHeaderFnRef) {
  return (
    <h3
      {...props}
      ref={ref}
      className="mb-2 block font-medium text-gray-700" />
  );
}

export const Label = Object.assign(forwardRef(LabelTextFn), {
  Heading: forwardRef(LabelHeaderFn),
});
